/*
* IcoMoon
*/
@import "bootstrap.min.css";
@font-face {
  font-family: 'icomoon';
  src: url('/web/fonts/icomoon.eot?7v7fny');
  src: url('/web/fonts/icomoon.eot?7v7fny#iefix') format('embedded-opentype'), url('/web/fonts/icomoon.ttf?7v7fny') format('truetype'), url('/web/fonts/icomoon.woff?7v7fny') format('woff'), url('/web/fonts/icomoon.svg?7v7fny#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
  font-family: 'icomoon';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-mail:before {
  content: "\e90a";
}
.icon-open_in_new:before {
  content: "\e90b";
}
.icon-menu:before {
  content: "\e906";
}
.icon-cross:before {
  content: "\e907";
}
.icon-twitter:before {
  content: "\e900";
}
.icon-twitter2:before {
  content: "\e901";
}
.icon-github:before {
  content: "\e902";
}
.icon-linkedin:before {
  content: "\e903";
}
.icon-linkedin2:before {
  content: "\e904";
}
.icon-stackoverflow:before {
  content: "\e905";
